lLL;;EAEE;AACF;EACI,YAAW;EACX,gBAAe,EAClB;;AACD;EACI,oBAAmB,EA0EtB;EA3ED;;;;;;IAQQ,gBAAe,EAClB;EATL;IAWQ,YAAW;IACX,gBAAe,EAClB;EAbL;IAeQ,oBAAmB,EAItB;IAnBL;MAiBY,+BXoB8B,EWnBjC;EAlBT;IAsBQ,qBAAoB;IACpB,aAAY,EACf;EAxBL;IA2BQ,iBAAgB;IAChB,iBAAgB;IAChB,iBAAgB;IAChB,wBAAuB,EAM1B;IApCL;MAgCY,iBAAgB;MAChB,iCXdkC;MWelC,iBAAgB,EACnB;EAnCT;IAsCQ,wCXb0C;IWc1C,2BXyC0B;IWxC1B,8CXf0C;IWgB1C,kBAAiB;IACjB,4BAA2B;IAC3B,0BAAyB;IACzB,oBAAmB;IACnB,gBAAe,EAMlB;IAnDL;MA+CY,gCXiCsB;MWhCtB,mCXvBsC;MWwBtC,8CXxBsC,EWyBzC;EAlDT;IAsDY,eAAc;IACd,gBAAe,EAClB;EAxDT;IA0DY,YAAW;IACX,kBAAiB;IACjB,mBAAkB,EACrB;EA7DT;IAgEQ,YAAW;IACX,kBAAiB;IACjB,mBAAkB,EACrB;EAnEL;IAqEQ,kBAAiB;IACjB,mBAAkB,EACrB;EAvEL;IAyEQ,kBAAiB,EACpB;;AAGL;EACI;;IAEI,gBAAe;IACf,eAAc;IACd,gBAAe,EAClB;EACD;;IAGQ,gBAAe;IACf,eAAc;IACd,gBAAe,EAClB;EANL;;;IAUQ,eAAc;IACd,gBAAe,EAClB,EAAA;;AZ9ET;EACI,mBAAkB,EACrB;;AAED;EACI,OAAM;EACN,WAAU;EACV,iBAAgB;EAChB,kBAAiB;EACjB,qCAAoC;EACpC,8BAA6B;EAC7B,6BAA4B,EAC/B;;AAED;EACI,eAAc,EACjB;;AAED;EACI,eAAc;EACd,aAAY;EACZ,aAAY;EACZ,SAAQ;EACR,UAAS;EACT,0BAAyB;EACzB,oBAAmB;EACnB,4BAA2B;EAC3B,wBAAuB;EACvB,gBAAe;EACf,oBAAmB,EACtB;;AAED;EACI,wBAAuB,EAC1B;;AAED;EACI,YAAW,EACd;;AAED;EACI,YAAW;EACX,kBAAiB;EACjB,qCAAoC;EACpC,kCAAiC;EACjC,6BAA4B,EAC/B","file":"client.scss","sourcesContent":["/*! -----------------------------------------------------------------\n[Master Stylesheet for Client]\n\nProject:     Ajaxtown\nVersion:     1.0\nLast change: 21/03/2017\nPrimary use: Blog\n\n------------------------------------------------------------------\n[Sidebar Styles]\n*/\n\n@import \"../css/bootstrap.min.css\";\n@import \"../css/vertical.css\";\n@import \"../css/font-awesome.min.css\";\n@import \"./common.scss\";\n@import \"./client/_layout.scss\";\n@import \"./client/_navbar.scss\";\n@import \"./client/_widgets.scss\";\n@import \"./common/_card.scss\";\n@import \"./common/_tags.scss\";\n@import \"./common/_sidebar.scss\";\n@import \"./common/_navbar.scss\";\n@import \"./common/_post.scss\";\n\n.dropdown-submenu {\n    position: relative;\n}\n\n.dropdown-submenu > .dropdown-menu {\n    top: 0;\n    left: 100%;\n    margin-top: -6px;\n    margin-left: -1px;\n    -webkit-border-radius: 0 6px 6px 6px;\n    -moz-border-radius: 0 6px 6px;\n    border-radius: 0 6px 6px 6px;\n}\n\n.dropdown-submenu:hover > .dropdown-menu {\n    display: block;\n}\n\n.dropdown-submenu > a:after {\n    display: block;\n    content: \" \";\n    float: right;\n    width: 0;\n    height: 0;\n    border-color: transparent;\n    border-style: solid;\n    border-width: 5px 0 5px 5px;\n    border-left-color: #ccc;\n    margin-top: 5px;\n    margin-right: -10px;\n}\n\n.dropdown-submenu:hover > a:after {\n    border-left-color: #fff;\n}\n\n.dropdown-submenu.pull-left {\n    float: none;\n}\n\n.dropdown-submenu.pull-left > .dropdown-menu {\n    left: -100%;\n    margin-left: 10px;\n    -webkit-border-radius: 6px 0 6px 6px;\n    -moz-border-radius: 6px 0 6px 6px;\n    border-radius: 6px 0 6px 6px;\n}\n",":root {\n    --color-text-primary: #333333;\n    --color-text-primary-invert: #b7b7b7;\n    --color-text-primary-light: #616161;\n    --color-text-muted: rgb(185, 185, 185);\n    --color-text-secondary: #000000;\n    --color-text-secondary-light: #4c4c4c;\n    --color-accent: #2196f3;\n    --color-bg-primary: #27282f;\n    --color-bg-secondary: #1f2129;\n    --color-border: #e5e5e5;\n    --link-hover: #1a82d6;\n    --color-menu-link: var(--color-text-primary-invert);\n}\n\n$racing_font: \"Racing Sans One\", cursive;\n\n$fs-small: 12px;\n$fs-normal: 14px;\n$fs-medium: 16px;\n$fs-large: 18px;\n\n/*!------------------------------------------------------------------\n[Body Color]\n*/\n\n$color-text-primary: var(--color-text-primary);\n\n/*!------------------------------------------------------------------\n[Headlines]\n*/\n\n$color-text-secondary: var(--color-text-secondary);\n\n/*!------------------------------------------------------------------\n[Help text]\n*/\n\n$color-text-primary-light: var(--color-text-primary-light);\n\n/*!------------------------------------------------------------------\n[Placeholders]\n*/\n\n$color-text-muted: var(--color-text-muted);\n\n/*!------------------------------------------------------------------\n[Contrast for backgrounds. This should be opposite of text-primary-color]\n*/\n\n$color-text-primary-invert: var(--color-text-primary-invert);\n\n/*!------------------------------------------------------------------\n[Input boxes, text areas, etc. All .form-control]\n*/\n\n$color-text-secondary-light: var(--color-text-secondary-light);\n\n/*!------------------------------------------------------------------\n[Something to contrast]\n*/\n\n$color-accent: var(--color-accent);\n\n/*!------------------------------------------------------------------\n[Body background color]\n*/\n\n$color-bg-primary: var(--color-bg-primary);\n\n/*!------------------------------------------------------------------\n[Sidebar color]\n*/\n\n$color-bg-secondary: var(--color-bg-secondary);\n\n/*!------------------------------------------------------------------\n[Link colors]\n*/\n\n$link_active: var(--color-accent);\n$link_hover: var(--link-hover);\n\n/*!------------------------------------------------------------------\n[Borders]\n*/\n\n$color-border: var(--color-border);\n\n/*!------------------------------------------------------------------\n[Menu Link Color]\n*/\n\n$color-menu-link: var(--color-menu-link);\n",".wrapper {\n    position: relative;\n    margin-left: 250px;\n}","@import url(\"https://fonts.googleapis.com/css?family=Source+Sans+Pro:100,300,400,500,600,700\");\n@import url(\"https://fonts.googleapis.com/css?family=Open+Sans:400,400italic,700\");\n@import url(\"http://cdn.jsdelivr.net/highlight.js/9.8.0/styles/monokai.min.css\");\n@import \"./variables.scss\";\n@import \"./common/layout.scss\";\n\n/*! -----------------------------------------------------------------\n[Master Stylesheet]\n\nProject:     Ajaxtown\nVersion:     1.0\nLast change: 21/03/2017\nPrimary use: Blog\n\n------------------------------------------------------------------\n[General Styles]\n*/\n\nhtml {\n    height: 100%;\n}\n\nbody {\n    background: $color-bg-primary;\n    font-family: \"Source Sans Pro\", sans-serif;\n    line-height: 1.6;\n    font-size: $fs-normal;\n    font-weight: 400;\n    color: $color-text-primary;\n    -ms-overflow-style: scrollbar;\n    overflow-x: hidden;\n    height: 100%;\n}\n\nimg {\n    max-width: 100%;\n    height: auto;\n}\n\niframe {\n    border: 0;\n}\n\n[contenteditable=\"true\"]:empty:before {\n    content: attr(placeholder);\n    display: block;\n    color: #ccc;\n    cursor: pointer;\n    /* For Firefox */\n}\n\ninput,\ntextarea,\n[contenteditable=\"true\"]:focus {\n    outline: none;\n}\ninput[type=\"search\"]::-webkit-search-cancel-button {\n    -webkit-appearance: searchfield-cancel-button;\n}\npre.CodeFlask__pre {\n    opacity: 0;\n}\ntextarea.CodeFlask__textarea {\n    opacity: 1;\n}\n\n.table {\n    tbody tr td:first-child,\n    thead tr th:first-child {\n        padding-left: 16px !important;\n    }\n}\n\n::-webkit-input-placeholder {\n    color: $color-text-muted !important;\n    font-weight: 300;\n}\n\n.top-head {\n    display: none;\n}\n\n.home .top-head {\n    display: block;\n}\n\n.container-fluid {\n    padding: 0 24px;\n}\n\n.pointer {\n    cursor: pointer;\n}\n\n.fa {\n    position: relative;\n}\n\n.fs-small {\n    font-size: 12px;\n}\n.fs-normal {\n    font-size: 14px;\n}\n.fs-medium {\n    font-size: 16px;\n}\n.fs-large {\n    font-size: 18px;\n}\n\n.hide {\n    display: none;\n}\n/*!------------------------------------------------------------------\n[Typography]\n*/\n\na {\n    color: $link_active;\n    -webkit-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -moz-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    -o-transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n    transition: all 0.125s cubic-bezier(0.3, 0.1, 0.58, 1);\n}\n\na:hover,\na:focus {\n    text-decoration: none;\n    outline: none;\n    color: $link_active;\n}\na:hover {\n    color: $link_hover;\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    font-family: \"Roboto Condensed\", sans-serif;\n    text-transform: uppercase;\n    letter-spacing: 1px;\n    font-weight: 400;\n    margin: 0 0 20px;\n}\n\nh1 {\n    font-size: 22px;\n}\n\nh2 {\n    font-size: 20px;\n}\n\nh3 {\n    font-size: 18px;\n}\n\nh4 {\n    font-size: 16px;\n}\n\nh5 {\n    font-size: 14px;\n}\n\nh6 {\n    font-size: 12px;\n}\n\np,\nul,\nol {\n    margin: 0 0 30px;\n}\n\npre {\n    border: 0;\n    line-height: 1.9;\n    font-size: 12px;\n    padding: 35px;\n    -webkit-border-radius: 2px;\n    -moz-border-radius: 2px;\n    -o-border-radius: 2px;\n    border-radius: 2px;\n}\n\nblockquote {\n    border: 0;\n    font-family: Georgia, sans-serif;\n    text-align: center;\n    font-style: italic;\n    font-size: 28px;\n    color: #111;\n    padding: 0;\n    margin: 0;\n}\n\nblockquote:before {\n    display: block;\n    content: \"\\f10d\";\n    margin: 0 0 30px;\n    font: normal normal normal 14px/1 FontAwesome;\n    font-size: 24px;\n    color: #bbb;\n    -webkit-font-smoothing: antialiased;\n    text-rendering: auto;\n    opacity: 0.3;\n}\n\nblockquote cite {\n    display: block;\n    font-size: 17px;\n    color: #bbb;\n    margin: 30px 0;\n}\n\nul {\n    padding-left: 0px;\n    list-style: none;\n}\n\n.lead {\n    line-height: 1.8;\n    font-size: 18px;\n}\n\n.font-alt {\n    text-transform: uppercase;\n    letter-spacing: 1px;\n}\n\n.font-serif {\n    font-family: Georgia, sans-serif;\n    font-style: italic;\n}\n\n.text-light {\n    color: #fff !important;\n}\n\n.text-light h1,\n.text-light h2,\n.text-light h3,\n.text-light h4,\n.text-light h5,\n.text-light h6 {\n    color: #fff !important;\n}\n\n.text-light a {\n    color: rgba(255, 255, 255, 0.7);\n}\n\n.text-light a:hover {\n    color: #fff;\n}\n\n/*!------------------------------------------------------------------\n[Forms]\n*/\n\n.form-control {\n    border: 0;\n    height: 36px;\n    padding: 13px 0px;\n    font-size: $fs-normal;\n    border-bottom: 1px solid $color-border;\n    color: $color-text-secondary-light;\n    border-radius: 0px;\n    -webkit-box-shadow: none;\n    -moz-box-shadow: none;\n    -o-box-shadow: none;\n    box-shadow: none;\n    transition: 0.3s border-color linear;\n    text-transform: none;\n    background: transparent;\n}\n\n.form-control[disabled] {\n    background: transparent;\n    color: $color-text-primary-light;\n}\n\n.form-control:focus {\n    box-shadow: 0px 1px 0px 0px #66afe9;\n}\n\n.comments-form .control-label {\n    font-weight: 400;\n    font-size: 12px;\n    color: #bbb;\n}\n\n/*!------------------------------------------------------------------\n[Notification Messages]\n*/\n#notification-wrapper {\n    font-size: $fs-normal;\n}\n\n/*!------------------------------------------------------------------\n[Modules]\n*/\n\n.module,\n.module-sm,\n.module-xs {\n    background-attachment: fixed;\n    padding: 140px 0;\n    position: relative;\n    background-repeat: no-repeat;\n    background-position: center center;\n    -webkit-background-size: cover;\n    -moz-background-size: cover;\n    -o-background-size: cover;\n    background-size: cover;\n}\n\n.module-sm {\n    padding: 70px 0;\n}\n\n.module-xs {\n    padding: 16px 0;\n    margin: 0 16px;\n}\n\n.module-title {\n    line-height: 1.4;\n    font-weight: 400;\n    font-size: 15px;\n    color: var(--color-text-secondary);\n    text-transform: uppercase;\n}\n\n.module-subtitle {\n    font-family: \"Roboto\", sans-serif;\n    line-height: 1.8;\n    font-weight: 300;\n    font-size: 13px;\n    color: $color-text-primary-light;\n}\n\n.module-title,\n.module-subtitle {\n    margin: 0 0 16px;\n}\n\n.module-title + .module-subtitle {\n    margin-top: -16px;\n}\n\n.divider {\n    border-color: #f5f5f5;\n    margin: 0;\n}\n\n.to-top-link {\n    position: absolute;\n    bottom: 0;\n    left: 50%;\n    width: 40px;\n    height: 40px